@import url(https://fonts.googleapis.com/css?family=Open Sans:300,400,600,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat+Alternates:300,400,600,700);
.header {
    width: 100%; 
    height: 54px; 
    background-color: #FFFFFF;
    border-bottom: 1px solid #e6e6e6;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-right: 9px
}

.header-logo {
    float: left;
    width: 36px;
    height: 36px;
    border-radius: 40px;
    margin-top: 4px;
    margin-left: 6px;
}

.header-title {
    float: left;
    font-size: 19px;
    font-weight: 600;
    color: #454545;
    margin-left: 10px;
    padding-top: 11px;
    padding-right: 8px;
    letter-spacing: 1.2px;
}

.team-select {
    float: left;
    height: 44px;
    margin-top: 5px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.team-select:hover {
    background-color: #e5f2f4;
}

.settings-icon {
    color: #454545;
    width: 20px;
    height: 20px;
}

.header-button {
    margin-top: 5px;
    margin-right: 1px;
    width: 44px;
    height: 44px;
    padding-left: 11px;
    padding-top: 11px;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    color: #454545;
    float: right;
    cursor: pointer;
}

.header-button:hover {
    background-color: #e5f2f4;
}

.chevron-down {
    margin-top: 14px;
    margin-right: 6px;
}
.room-block{
    height : 45px;
    background : #ffffff;
}

.room-logo {
    float: left;
    width: 36px;
    height: 36px;
    border-radius: 6px;
    margin-top: 6px;
    margin-bottom: 6px;
    margin-left: 16px;
    cursor: pointer;
}

.room-title {
    float: left;
    font-size: 15px;
    font-weight: 650;
    color: #454545;
    margin-left: 10px;
    padding-top: 15px;
    padding-right: 8px;
    letter-spacing: 1.2px;
    cursor: pointer;
}

.lock-room-button{
    float: right;
    border-radius: 5px;
    margin-top: 6px;
    margin-right: 27px;
    width: 20px;
    height: 20px;
    padding: 11px;
    cursor: pointer;
}
.drop-down {
    background-color : #e6e6e6;
    height : 44px;
    color: #5b5b5b;
    font-weight: 600;
    font-size: 14px;
    padding-left: 16px;
    padding-right: 14px;
    cursor: pointer;
}

.add-icon {
    width: 14px;
    height: 14px
}

.create-room-button {
    float: right;
    border-radius: 5px;
    margin-top: 4px;
    width: 36px;
    height: 36px;
    padding: 11px;
    cursor: pointer;
}

.create-room-button:hover {
    /* background-color: #b5b5b5;
    color: #292929; */
    background-color: #d4d4d4;
    color: #292929
}

.drop-down-title {
    padding-top: 14px;
    float: left;
}

.search-bar {
    height: 28px;
    background-color: #E7E7E7;
    color: #454545;
    width: 100%;
    height: 30px;
    border-radius: 5px;
}

.search-bar:hover {
    background-color: #E7E7E7;
}

.search-bar > input {
    height: 100%;
    width: 100%;
    font-size: 14px;
    color: #000000;
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: #E7E7E7;
    padding-left: 33px;
}

.search-bar > input:hover {
    background-color: #E0E0E0;
}

.search-bar > input:focus {
    outline: none;
    border: 1px solid #add2ef;
    background-color: #E7E7E7;
}

.search-icon {
    position: absolute;
    margin-left: 9px;
    margin-top: 6px;
    color: #454545
}
.control-bar{
    background-color: #ffffff;
    /* color: antiquewhite; */
    width: 100%;
    height: 38px;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 6px;
    padding-right: 6px;
    display: inline-flex;
    justify-content: space-between;
}

.icon-red {
    width: 22px;
    height: 22px;
    color: #ea0f0f;
}

.icon-green {
    width: 22px;
    height: 22px;
    color: #3cf036;
}

.icon {
    width: 22px;
    height: 22px;
    color: #454545;
}

.media-button{
    background-color: #ffffff;
    position: relative;
    width: 48px;
    height: 35px;
    border-radius: 2px;
    margin-right: 1px;
    margin-left: 1px;
    padding-left: 13px;
    padding-top: 6px;
    float:left;
    /* align-self: flex-start; */
    cursor: pointer;
}

.media-button:hover{
    background-color: #ddefec;
}

.right-button {
    background-color: #ffffff;
    position: relative;
    width: 48px;
    height: 35px;
    border-radius: 2px;
    margin-right: 1px;
    margin-left: 1px;
    padding-left: 13px;
    padding-top: 6px;
    float:right;
    /* align-self: flex-end; */
    cursor: pointer;
}

.right-button:hover {
    background-color: #ddefec;
}

.leave-button {
    background-color: #ed0d0d;
    font-size: 14px;
    border-radius: 5px;
    width: 86px;
    height: 23px;
    margin-top: 5px;
    padding-top: 3px;
    display: inline-block;
    text-align: center;
    color: rgb(245, 245, 245);
    box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2), 0 4px 3px -3px rgba(0, 0, 0, 0.19);
    cursor: pointer;
}

.leave-button:hover {
    background-color: #ff0000;
    box-shadow: gray;
    color: rgb(255, 255, 255);
}
/* @import url('https://fonts.googleapis.com/css?family=Lato:300,400,700'); */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

*.unselectable {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

::-webkit-scrollbar {
  display: none;
}

.App {
  background-color: #FFFFFF;
  height: 100vh;
  width: 100vw;
  white-space: nowrap;
}

.line-horizontal-white {
  height: 1px;
}

.line-horizontal-gray {
  height: 1px;
  background-color: #dadada
}

.scroll-content {
  position: fixed;
  top: 54px;
  left: 0px;
  right: 0px;
  bottom: 156px;
  width:inherit;
  border-right: 1px solid #dadada;
}

.control-bar-footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  height: 38px;
  border-top: 1px solid #e6e6e6;
  width:inherit;
}

.video-region {
  position: fixed;
  bottom: 38px;
  background-color: #323232;
  color: #E0E0E0;
  padding-top: 2px;
  padding-bottom: 2px;
  height: 118px;
  width: inherit;
}
.Login {
    height: 100vh;
    width: 100vw;
    padding: 24px;
    background-color: white;
}

.logo_header {
    color: #222e64;
    text-align: center;
    font-size: 30px;
    font-weight: 600;
    letter-spacing: 1.2px;
    padding-bottom: 22px;
    font-family: 'Montserrat Alternates';
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Login > input {
    height:44px;
    width: 100%;
    font-size: 14px;
    color: #1e1e1e;
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: #E7E7E7;
    padding-left: 20px;
    font-weight: 300;
}

.Login > input:hover {
    background-color: #E0E0E0;
}

.Login > input:focus {
    outline: none;
    border: 1px solid #add2ef;
    background-color: #E7E7E7;
}

.bottom-text {
    font-size: 13px;
    font-weight: 300;
    color: #454545;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.login-button {
    background-color: #9FC2E3;
    border-radius: 3px;
    border: 0px;
    width: 100%;
    height: 44px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1.5px;
    color: white;
    text-align: center;
    padding-top: 14px;
    box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.2), 0 6px 4px -4px rgba(0, 0, 0, 0.19);
}

.login-button:hover {
    background-color: #ADD1EC;
    box-shadow: gray;
}
