.header {
    width: 100%; 
    height: 54px; 
    background-color: #FFFFFF;
    border-bottom: 1px solid #e6e6e6;
    user-select: none;
    padding-right: 9px
}

.header-logo {
    float: left;
    width: 36px;
    height: 36px;
    border-radius: 40px;
    margin-top: 4px;
    margin-left: 6px;
}

.header-title {
    float: left;
    font-size: 19px;
    font-weight: 600;
    color: #454545;
    margin-left: 10px;
    padding-top: 11px;
    padding-right: 8px;
    letter-spacing: 1.2px;
}

.team-select {
    float: left;
    height: 44px;
    margin-top: 5px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.team-select:hover {
    background-color: #e5f2f4;
}

.settings-icon {
    color: #454545;
    width: 20px;
    height: 20px;
}

.header-button {
    margin-top: 5px;
    margin-right: 1px;
    width: 44px;
    height: 44px;
    padding-left: 11px;
    padding-top: 11px;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    color: #454545;
    float: right;
    cursor: pointer;
}

.header-button:hover {
    background-color: #e5f2f4;
}

.chevron-down {
    margin-top: 14px;
    margin-right: 6px;
}